
:root {
  --BackgroundHue: 235;
  --BackgroundSaturation: 10%;
  --BackgroundLightness: 20%;
}

body {
  color: hsla(0, 0%, 100%, 0.9);
  font-family: "Fira Code", monospace;
  text-decoration: none;
  line-height: 1.4;
  /* background-color: rgba(0, 0, 0, 0); */
  background-color: hsl(var(--BackgroundHue),var(--BackgroundSaturation),var(--BackgroundLightness));
  padding: 0;
  justify-content: center;
}

@media screen and (max-width: 1280px) {
   .navBar {
    display: grid !important;
  }
  .Home p {
    font-size: 20px;
  }
}

@media screen and (max-width: 980px) {
  .Home p {
    font-size: 18px;
  }
}

@media screen and (max-width: 736px) {
  .Home p, .Project {
    font-size: 14px !important;
  }
}

@media screen and (max-width: 480px) {
  .Home p {
    font-size: 12px;
  }
}

#root.Home {
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 1.5),rgba(0, 0, 0, 0.3)), url('./img/Spaceinvader_c++.gif') !important;
  background-size: cover;
  margin: 0!important;
}

/*Header css start*/
header h1 {
  text-align: center;
  font-style: italic;
}
/*Header css end*/

/*menuItem css start*/
.navBar {
  display: flex;
  justify-content: center;
  align-content: center;
}

.MenuLink{
  color: white;
}

.MenuLink:hover {
  color: grey;
  transition: all ease-in-out;
  cursor: pointer;
}

.navBar li {
  font-size: 1.25rem;
  display: inline-block;
  margin-right: 80px;
  margin-left: 80px;
}
/*menuItem css end*/

/*home css start*/
.Home img {
  border-radius: 1.25rem;
  margin-right: 1%;
  max-height: 200px;
  min-height: 200px;
  max-width: 150px;
  min-width: 150px;
}

.Home {
 display: flex;
 flex-wrap: wrap;
 justify-content: center;
 margin-top: 100px;
 font-size: 24px;
}

.Home p {
  max-width: 50%;
  text-size-adjust: 100%;
}
/*home css end*/


/*Cv css start */
.CV {
  display: flex;
  justify-content: center;
}

.CV img {
  margin-top: 50px;
}
/*Cv css end */


/*Projectspreview.jsx css start--->*/
.container {
  display: flex;
  justify-content: center;
}


.ProjectsPreviewContainer {
  display: grid;
  grid-template-columns: 50% 50%;
  row-gap: 15px;
  column-gap: 20px;
  justify-content: center;
  margin-top: 50px;
  max-width: 60%;
}

.ProjectPreview {
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.ProjectPreview img {
  max-width: 100%;
  height: 250px;
  object-fit: cover;
}

.ProjectPreview:hover {
  transform: scale(1.05);
  transition: all 0.3s ease-in-out;
}

.ProjectPreview h3,.ProjectPreview p {
  color: white;
}
/*<--Projectpreview.jsx css end*/


.Project {
  display: grid;
  justify-content: center;
  font-size: 18px;
}

.Project h1,.Project h2,.Project h3,.Project h4,.Project h5,.Project h6,.Project body {
  background-color: hsl(var(--BackgroundHue),var(--BackgroundSaturation),var(--BackgroundLightness));
  color: white;
  font-family:'nixie_oneregular', 'Courier New', Courier, monospace;
  text-size-adjust: 100%;
  margin-top: 20px;
}

.Project h1,.Project h2,.Project h3 {
  border-bottom: 2px solid currentColor;
}

.Project img {
  display: flex;
  justify-content: center;
  max-width: 100%;
  height: 350px;
  object-fit: cover;
  padding-top: 10px;
  padding-bottom: 10px;
  margin: auto;
}

.Project .react-player {
  margin: auto;
}

.Project #bigImage {
  width: 100% !important;
  height: 100% !important;
  max-width: 50% !important;
}

.Project #centerize {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/*Accordion css start ->*/
.card{
  background-color: hsl(var(--BackgroundHue),var(--BackgroundSaturation),var(--BackgroundLightness));
  border: transparent;
  display: block !important;
}

.card-header {
  padding: .75rem 1.25rem;
  background-color: rgb(66, 66, 66);
  color: white;
  cursor: pointer;
  text-align: center;
  font-weight: bold;
  font-size: 24px;
}

.card-body {
  color: transparent;
  background-color: transparent;
}
/*<- Accordion css end*/

footer{
  bottom: 0;
  width: 100%;
  padding-top: 50px;
  text-align: center;
  font-size: 9pt;
  
}